import React from 'react'

import { DEFAULTS } from '../../config/config'
import Devices from '../../Devices'
import Settings from '../../Settings'
import Connection from '../../Connection'
import i18n from '../../i18n'

import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Typography from '@material-ui/core/Typography'

import Dialog from '../../components/Dialog'
import Inputs from '../../components/Inputs'

import DevicesListPage from './DevicesPage/DevicesListPage'
import LayoutsPage from './LayoutsPage/LayoutsPage'
import SettingsPage from './SettingsPage/SettingsPage'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	paper: {
		marginBottom: '10px'
	},
	pinToTop: {
		position: 'absolute',
		top: 0,
		left: 0
	},
	title: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
		marginBottom: theme.spacing(1),
		minHeight: '64px',
		display: 'flex',
		position: 'relative',
		alignItems: 'center'
	},
	rawEditor: {
		fontFamily: 'Roboto Mono,Consolas,Courier New,monospace !important'
	}
});


class ConfigurationPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		let settings = { ...Settings.getSettings() };
		let layout = settings.layout;
		delete settings.layout;
		delete settings.iobroker;
		
		this.state = {
			expertMode: false,
			tab: 'devices',
			errors: {},
			
			settings: settings || {},
			layout: layout || {},
			devices: Devices.getDevices({ raw: true, copy: true })
		}
		
		this.socket = Connection.getConnection;
		
		this._onDevicesChange.bind(this);
		this._onSettingsChange.bind(this);
		this._onLayoutsChange.bind(this);
	}
	
	/*
	 *
	 *
	 */
	componentDidUpdate(prevProps, prevState) {
		
		if (this.state.expertMode) {
			let rawEditor = document.getElementById(this.state.tab);
			
			if (rawEditor.scrollHeight !== prevState.rawEditorHeight) {
				this.setState({ rawEditorHeight: rawEditor.scrollHeight });
			}
		}
	}
	
	/*
	 * 
	 */
	_onDevicesChange = devices => this.setState({ devices: devices });
	_onSettingsChange = settings => this.setState({ settings: settings });
	_onLayoutsChange = (layout, settings) => this.setState(prevState => ({ settings: { ...prevState.settings, ...settings || {} }, layout: layout }));
	
	/*
	 *
	 */
	onRawChange = (props, val) => {
		
		try {
			let res = JSON.parse(val);
			
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors }
				delete nextErrors[props.id];
				
				return {
					errors: nextErrors,
					[props.type]: res
				}
			});
		}
		catch(err) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					[props.id]: i18n.t('Invalid JSON format')
				}
			}));
		}
	}
	
	/**
	 * 
	 */
	_handleTabChange = (event, tab) => this.setState({ tab });
	
	/**
	 *
	 */
	_cancel = () => this.props.navigate('home');
	
	/**
	 *
	 */
	_save = () => {
		let result = [
			this.socket.setState(DEFAULTS.NODE_SETTINGS, JSON.stringify({ ...this.state.settings, layout: this.state.layout })),
			this.socket.setState(DEFAULTS.NODE_DEVICES, JSON.stringify(this.state.devices))
		];
		
		Promise.all(result)
			.then(() => window.location.reload())
			.catch(err => console.error(err));
	}
	
	/*
	 *
	 */
	render() {
		const { classes } = this.props;
		
		return (

<Dialog
	classes={{ CustomDialog: classes.pinToTop }}
	fullWidth={true}
	maxWidth={false}
	
	open={(this.props.page === 'configuration')}
	onClose={this._cancel}
	
	header={[
		<Typography key="header" variant="h6">
			{i18n.t('Configuration')}
			<Inputs.SwitchInline
				key="Input-expertMode"
				id="expertMode"
				size="small"
				label={i18n.t('Expert mode')}
				onChange={(key, val) => this.setState(prevState => ({ expertMode: val, tab: prevState.expertMode ? prevState.tab.substr(0, prevState.tab.length-3) : prevState.tab + 'Raw' }))}
				val={this.state.expertMode}
				/>
		</Typography>,
		
		<Tabs
			key="tabs"
			value={this.state.tab}
			indicatorColor="primary"
			textColor="primary"
			onChange={this._handleTabChange}
			>
			
			{this.state.expertMode === true && <Tab value="devicesRaw" label={i18n.t('Devices')} />}
			{this.state.expertMode === true && <Tab value="layoutRaw" label={i18n.t('Layout')} />}
			{this.state.expertMode === true && <Tab value="settingsRaw" label={i18n.t('Settings')} />}
			{this.state.expertMode !== true && <Tab value="devices" label={i18n.t('Devices')} />}
			{this.state.expertMode !== true && <Tab value="layout" label={i18n.t('Layout')} />}
			{this.state.expertMode !== true && <Tab value="settings" label={i18n.t('Settings')} />}
		</Tabs>
	]}
	actions={[
		<Button key='action-cancel' autoFocus onClick={this._cancel} color="secondary">
			{i18n.t('Cancel')}
		</Button>,
		<Button key='action-save' onClick={this._save} color="primary" variant="contained" disabled={Object.keys(this.state.errors).length > 0}>
			{i18n.t('Save')}
		</Button>
	]}
	>
	
	<div hidden={this.state.tab !== 'devices'}>
		<Paper className={classes.paper}>
			{this.state.tab === 'devices' && <DevicesListPage onChange={this._onDevicesChange} />}
		</Paper>
	</div>
	{this.state.expertMode === true && 
	<div hidden={this.state.tab !== 'devicesRaw'}>
		<Paper className={classes.paper}>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Raw Editor') + ' ' + i18n.t('Devices')}
			</Typography>
			<Inputs.Text
				id="devicesRaw"
				label={['JSON Editor', (this.state.errors['devicesRaw'] !== undefined ? ' (' + this.state.errors['devicesRaw'] + ')' : '')]}
				InputProps={{ disableUnderline: true, className: classes.rawEditor }}
				inputProps={{ style: { height: this.state.rawEditorHeight } }}
				multiline
				width="99%"
				error={this.state.errors['devicesRaw'] !== undefined}
				onChange={(props, val) => this.onRawChange({ ...props, type: 'devices' }, val)}
				val={JSON.stringify(this.state.devices, null, 3)}
				/>
			
		</Paper>
	</div>}
	<div hidden={this.state.tab !== 'settings'} style={{ width: '100%' }}>
		<Paper className={classes.title}>
			<Typography variant="h6">
				{i18n.t('Settings')}
			</Typography>
		</Paper>
		{this.state.tab === 'settings' && <SettingsPage settings={this.state.settings} onChange={this._onSettingsChange} />}
		
	</div>
	{this.state.expertMode === true && 
	<div hidden={this.state.tab !== 'settingsRaw'} style={{ width: '100%' }}>
		<Paper className={classes.paper}>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Raw Editor') + ' ' + i18n.t('Settings')}
			</Typography>
			<Inputs.Text
				id="settingsRaw"
				label={['JSON Editor', (this.state.errors['settingsRaw'] !== undefined ? ' (' + this.state.errors['settingsRaw'] + ')' : '')]}
				InputProps={{ disableUnderline: true, className: classes.rawEditor }}
				inputProps={{ style: { height: this.state.rawEditorHeight } }}
				multiline
				width="99%"
				error={this.state.errors['settingsRaw'] !== undefined}
				onChange={(props, val) => this.onRawChange({ ...props, type: 'settings' }, val)}
				val={JSON.stringify(this.state.settings, null, 3)}
				/>
			
		</Paper>
	</div>}
	<div hidden={this.state.tab !== 'layout'}>
		<Paper>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Layout')}
			</Typography>
		</Paper>
		{this.state.tab === 'layout' && <LayoutsPage settings={this.state.settings} layout={this.state.layout} onChange={this._onLayoutsChange} />}
		
	</div>
	{this.state.expertMode === true && 
	<div hidden={this.state.tab !== 'layoutRaw'} style={{ width: '100%' }}>
		<Paper className={classes.paper}>
			<Typography className={classes.title} variant="h6">
				{i18n.t('Raw Editor') + ' ' + i18n.t('Layout')}
			</Typography>
			<Inputs.Text
				id="layoutRaw"
				label={['JSON Editor', (this.state.errors['layoutRaw'] !== undefined ? ' (' + this.state.errors['layoutRaw'] + ')' : '')]}
				InputProps={{ disableUnderline: true, className: classes.rawEditor }}
				inputProps={{ style: { height: this.state.rawEditorHeight } }}
				multiline
				width="99%"
				error={this.state.errors['layoutRaw'] !== undefined}
				onChange={(props, val) => this.onRawChange({ ...props, type: 'layout' }, val)}
				val={JSON.stringify(this.state.layout || {}, null, 3)}
				/>
			
		</Paper>
	</div>}
	
</Dialog>

		)
	}
}

export default withStyles(styles)(ConfigurationPage);
