import React from 'react'
import clsx from 'clsx'
import i18n from '../../i18n'

import Divider from '@material-ui/core/Divider'
import Grid from '@material-ui/core/Grid'
import { MuiPickersUtilsProvider } from '@material-ui/pickers'
import Typography from '@material-ui/core/Typography'

/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	noPadding: {
		padding: '0 !important'
	},
	Fieldset: {
		padding: theme.spacing(0.5, 3, 2, 3)
	},
	FieldsetItem: {
		padding: theme.spacing(2, 0, 1, 0)
	},
	Legend: {
		fontSize: '1.2rem',
		color: theme.palette.primary.dark,
		padding: theme.spacing(1, 3),
	},
}));

/*
 *
 */
export default function Fieldset(props) {
	const { children, noPadding, ...otherProps } = props;
	const classes = useStyles();
	
	return (
		<>
			<FieldsetLegend {...otherProps} />
			<Grid container className={clsx(classes.Fieldset, noPadding && classes.noPadding)} {...otherProps}>
				{children}
			</Grid>
		</>
	);
}

/*
 *
 */
export function FieldsetItem(props) {
	const { container, children, columns } = props;
	const classes = useStyles();
	
	return (
		<Grid
			container={container || false}
			item
			className={classes.FieldsetItem}
			xs={columns || 12}
			{...props}
			>
			
			{children}
		</Grid>
	);
}

/*
 *
 */
export function FieldsetLegend(props) {
	const classes = useStyles();
	return !props.label ? null : (
		<>
			<Typography className={classes.Legend} variant="h3">{props.label}</Typography>
			<Divider />
		</>
	);
}

/*
 *
 */
export function Form(props) {
	const { children } = props;
	
	return (
		<MuiPickersUtilsProvider utils={i18n.getLocaleDateAdapter()} locale={i18n.getLocale()}>
			{children}
		</MuiPickersUtilsProvider>
	);
}