import React from 'react'
//import clsx from 'clsx'
//import { v4 as uuid } from 'uuid'

import DevicesConfig, { DevicesStates } from './Devices.config'
import i18n from '../../../i18n'
import Devices from '../../../Devices'
import FUNCTIONS from '../../../functions'
import ucFirst from '../../../helpers/utils'

import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'

import IconButton from '@material-ui/core/IconButton'
import Tooltip from '@material-ui/core/Tooltip'

import AddIcon from '@material-ui/icons/Add'
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import DeleteIcon from '@material-ui/icons/Delete'
import InfoIcon from '@material-ui/icons/Info'

import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	fullWidth: {
		width: '100%',
	},
	iconContainer: {
		flexDirection: 'column',
		alignItems: 'center'
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	}
});


class SettingsPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			device: this.props.device,
			errors: {}
		}
		
		// required fields
		if (!this.state.device.name) {
			this.state.errors['device-name'] = i18n.t('No input given');
		}
		if (!this.state.device.options || !this.state.device.options.group || (this.state.device.options.group && this.state.device.options.group.length === 0)) {
			this.state.errors['options-group'] = i18n.t('No groups selected');
		}
		
		//
		this.onChange = this.props.onChange;
		this.deviceId = this.props.device.id;
		this.groupEditor = {};
		this.newStateKey = '';
	}
	
	/*
	 *
	 */
	_save = () => {
		let savedDevice = { ...this.state.device }
		
		for (const key in this.groupEditor) {
			const val = this.groupEditor[key];
			const [ type, base, characteristic ] = key.split('-');
			
			// states
			if (type === 'states') {
				if (typeof savedDevice[type][base] === 'object') {
					savedDevice[type][base][characteristic] = val;
				}
				else {
					savedDevice[type][base] = {
						state: savedDevice[type][base],
						[characteristic]: val
					}
				}
			}
			
			// options or attributes
			else if (type === 'options' || type === 'attributes') {
				savedDevice[type][base] = val;
			}
			
			// device
			else {
				savedDevice[base] = val;
			}
		}
		
		// add new groups to list
		savedDevice.options.group.map(group => Devices.addGroupId(group.toLowerCase().replace(/ /, ''), group));
		
		// save
		savedDevice._created = Date.now();
		this.props.onSave && this.props.onSave(savedDevice);
	}
	
	/*
	 *
	 */
	_deleteDevice = () => this.props.onDelete && this.props.onDelete(this.deviceId);
	
	/*
	 *
	 */
	_onChange = (props, val) => {
		
		// data verification and error handler
		if (props.required && ((Array.isArray(val) && val.length === 0) || (typeof val === 'string' && !val))) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					[props.id]: i18n.t('No input given')
				}
			}));
			
			return false;
		}
		
		// parse JSON
		if (props.attributes && props.attributes.json) {
			
			try {
				val = val ? JSON.parse(val) : '';
			}
			catch(err) {
				this.setState(prevStates => ({
					errors: {
						...prevStates.errors,
						[props.id]: i18n.t('Wrong JSON format')
					}
				}));
				
				return false;
			}
		}
		
		// remove old errors
		if (this.state.errors[props.id]) {
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors || {} }
				delete nextErrors[props.id];
				return { errors: nextErrors };
			});
		}
		
		// set value
		this.groupEditor[props.id] = val;
	}
	
	/*
	 *
	 */
	_addState = () => {
		
		// empty state key
		if (!this.newStateKey) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					newStateKey: i18n.t('Type in a state key / identifier')
				}
			}));
		}
		
		// state key exists already
		else if (Object.keys(this.state.device.states).indexOf(this.newStateKey) > -1) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					newStateKey: i18n.t('State key already used')
				}
			}));
		}
		
		// add state key
		else {
			let key = this.newStateKey.replace(/ /g, '');
			//this.newStateKey = '';
			
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors }
				delete nextErrors['newStateKey'];
				
				return {
					errors: nextErrors,
					device: {
						...prevStates.device,
						states: {
							[key]: {},
							...prevStates.device.states || {}
						}
					}
				}
			});
		}
	}
	
	/*
	 *
	 */
	_moveState = (action, key) => {
		let order = { ...this.state.device.states }
		let sorted = false;
		let sortedArray = Object.keys(order).sort((a, b) => {
			
			if (!sorted && ((action === 'up' && b === key) || (action === 'down' && a === key))) {
				sorted = true;
				return 1;
			}
			
			return 0;
		});
		
		let sortedObject = {}
		sortedArray.map(key => sortedObject[key] = order[key]);
		
		this.setState(prevState => ({
			device: {
				...prevState.device,
				states: sortedObject
			}
		}));
	}
	
	/*
	 *
	 */
	_deleteState = key => {
		
		this.setState(prevStates => {
			let nextDevice = { ...prevStates.device }
			delete nextDevice.states[key];
			
			return {
				device: nextDevice
			}
		});
	}
	
	/*
	 *
	 */
	renderDeviceState(state) {
		const { classes } = this.props;
		
		const states = Object.keys(DevicesStates).map(key => {
			const s = DevicesStates[key];
			return (
				<Inputs.FieldsetItem columns={4} key={'states-' + state.key + '-' + key}>
					<Inputs.Text
						attributes={s}
						id={'states-' + state.key + '-' + key}
						shrink={true}
						label={s.label || ucFirst(key)}
						val={!state[key] || typeof state[key] === 'string' ? (state[key] || '') : (state[key].node || JSON.stringify(state[key]))}
						onChange={this._onChange}
						error={this.state.errors['states-' + state.key + '-' + key] !== undefined}
						/>
						
				</Inputs.FieldsetItem>
			);
		});
		
		return (
			<ListItem key={'StatesListItemLabel-' + state.key} dense divider>
				{state.key &&
				<ListItemIcon classes={{ root: classes.iconContainer }}>
					<IconButton size="small" onClick={() => this._moveState('up', state.key)}>
						<ArrowUpIcon />
					</IconButton>
					<IconButton size="small" onClick={() => this._moveState('down', state.key)}>
						<ArrowDownIcon />
					</IconButton>
					<IconButton size="small" onClick={() => this._deleteState(state.key)} style={{ marginTop: 40 }}>
						<DeleteIcon />
					</IconButton>
				</ListItemIcon>}
				
				<ListItemText disableTypography>
					<div style={{ paddingBottom: 8 }}><strong>{state.key}</strong></div>
					<Inputs.Fieldset noPadding>
						{states}
					</Inputs.Fieldset>
				</ListItemText>
			</ListItem>
		);
	}
	
	/*
	 *
	 */
	renderDeviceStates(states) {
		const { classes } = this.props;
		
		const renderedStates = Object.keys(states || {}).map(stateKey => {
			let state = states[stateKey];
			state = typeof state === 'string' ? { 'state': state } : state;
			return this.renderDeviceState({ ...state, key: stateKey });
		});
		
		return (
			<>
				<List classes={{ root: classes.fullWidth }} dense>
					<ListItem key={'StatesListItemLabel-add'} dense divider>
						<ListItemIcon>
							<IconButton size="small" disabled>
								<AddIcon />
							</IconButton>
						</ListItemIcon>
						
						<ListItemText disableTypography>
							<Inputs.Text id={'states-add'} onChange={(props, input) => this.newStateKey = input} shrink={true} label="new key" val={this.newStateKey} />
							<Inputs.Button
								label={i18n.t('add new state')}
								onClick={this._addState}
								/>
							
							{this.state.errors['newStateKey'] || ''}
						</ListItemText>
					</ListItem>
					
					{renderedStates}
					
				</List>
			</>
		);
	}
	
	/*
	 *
	 */
	renderDeviceOption(config, value) {
		
		if (Array.isArray(value)) {
			value = value.join(',');
		}
		else if (typeof value === 'object') {
			value = JSON.stringify(value);
		}
		
		const Tag = Inputs[config.type || 'Text'];
		return (
			<ListItem key={'StatesListItemLabel-' + config.parameter} divider>
				<ListItemText
					primary={config.parameter}
					secondary={i18n.t(config.label || '')}
					/>
					
				<ListItemSecondaryAction>
					<Tag
						key={'options-' + config.parameter}
						id={'options-' + config.parameter}
						
						val={value}
						onChange={this._onChange}
						{...config}
						/>
					
				</ListItemSecondaryAction>
			</ListItem>
		);
	}
	
	/*
	 *
	 */
	renderDeviceOptions(options) {
		const { classes } = this.props;
		const renderedOptions = Object.keys(DevicesConfig).sort().map(config => {
			
			if (config === 'group') {
				return null;
			}
			
			return this.renderDeviceOption({ ...DevicesConfig[config], parameter: config }, options[config] || '');
		});
		
		return (
			<List classes={{ root: classes.fullWidth }} dense>
				{renderedOptions}
			</List>
		);
	}
	
	/*
	 *
	 */
	render() {
		const { classes } = this.props;
		
		if (this.state.device === null) {
			return null;
		}
		
		const states = this.renderDeviceStates(this.state.device.states);
		const options = this.renderDeviceOptions(this.state.device.options);
		
		return (
			<Dialog
				header={i18n.t('Device Configuration')}
				actions={[
					<Grid key='actions' container>
						<Grid item xs={6}>
							<Button key='action-delete' onClick={() => this._deleteDevice()} color="secondary" startIcon={<DeleteIcon />}>
								{i18n.t('Delete Device')}
							</Button>
						</Grid>
						<Grid item xs={6} className={classes.alignRight}>
							<Button key='action-cancel' onClick={() => this.props.onClose()} color="secondary">
								{i18n.t('Cancel')}
							</Button>
							<Button key='action-save' onClick={() => this._save()} color="primary" disabled={Object.keys(this.state.errors).length > 0}>
								{i18n.t('Apply')}
							</Button>
						</Grid>
					</Grid>
				]}
				
				dense
				maxWidth='md'
				fullWidth={true}
				
				open={this.state.device !== null}
				onClose={() => this.props.onClose()}
				>
				
				<Inputs.Fieldset label={i18n.t('General')}>
					<Inputs.FieldsetItem columns={4}>
						<Inputs.Text
							key="device-name"
							id="device-name"
							autoFocus
							required
							error={this.state.errors['device-name'] !== undefined}
							
							label={i18n.t('Device')}
							val={this.state.device.name}
							onChange={this._onChange}
							/>
						
					</Inputs.FieldsetItem>
					<Inputs.FieldsetItem columns={4}>
						<Inputs.Text
							key="device-options-label"
							id="device-options-label"
							
							label={i18n.t('Displayed Name')}
							val={(this.state.device.options && this.state.device.options.label) || ''}
							onChange={this._onChange}
							/>
						
					</Inputs.FieldsetItem>
					<Inputs.FieldsetItem columns={4}>
						<Inputs.Select
							key="device-function"
							id="device-function"
							
							label={i18n.t('Function')}
							val={this.state.device.function}
							selection={Object.keys(FUNCTIONS).sort().map(func => func !== 'defaults' ? ({ [func]: i18n.t(func) }) : ({ 'other': i18n.t('other') }))}
							onChange={this._onChange}
							/>
						
					</Inputs.FieldsetItem>
					<Inputs.FieldsetItem>
						<Inputs.ChipsField
							width="100%"
							id="options-group"
							required
							error={this.state.errors['options-group'] !== undefined}
							
							placeholder={i18n.t('Groups')}
							val={(this.state.device.options && this.state.device.options.group && this.state.device.options.group.map(group => ({ id: group.toLowerCase(), name: group }))) || []}
							
							options={Devices.getGroupList()}
							optionKey="name"
							
							onChange={(props, input) => this._onChange(props, input.map(option => option.name))}
							/>
						
					</Inputs.FieldsetItem>
					
				</Inputs.Fieldset>
				
				<Inputs.Fieldset label={i18n.t('States')}>
					{states}
				</Inputs.Fieldset>
				
				<Inputs.Fieldset label={i18n.t('Options')}>
					{options}
				</Inputs.Fieldset>
			</Dialog>

		)
	}
}

export default withStyles(styles)(SettingsPage);
