import React, { useState } from 'react'
import i18n from '../../i18n'

import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import InputLabel from '@material-ui/core/InputLabel'
import Switch from '@material-ui/core/Switch'

import Field from './Field'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	FormControl: {
		width: '100%'
	},
	InputLabel: {
		width: '133%'
	},
	Switch: {
		marginTop: '10px'
	},
	SwitchSmall: {
		marginTop: '2px'
	},
	SwitchInline: {
		marginLeft: '5px'
	},
}));


/*
 *
 */
export default function InputSwitch(props) {
	const { id, label, val, size, disabled, onChange } = props;
	const classes = useStyles(props);
	
	const [checked, setChecked] = useState(val || false);
	const handleSwitchChange = event => {
		let userInput = (event && event.target && event.target.checked) || false;
		
		setChecked(userInput);
		onChange && onChange(props, userInput);
	}
	
	return (
		<Field {...props}>
			<FormControl
				key={'FormControl-' + id}
				classes={{ root: classes.FormControl }}
				>
				
				<InputLabel classes={{ root: classes.InputLabel }} shrink={true}>{i18n.t(label)}</InputLabel>
				<Switch
					classes={{ root: size === 'small' ? classes.SwitchSmall : classes.Switch }}
					checked={checked}
					disabled={disabled}
					onChange={handleSwitchChange}
					size={size}
					/>
				
			</FormControl>
		</Field>

	)
}

/*
 *
 */
export function InputSwitchInline(props) {
	const { label, val, size, disabled, onChange } = props;
	const classes = useStyles(props);
	
	const [checked, setChecked] = useState(val || false);
	const handleSwitchChange = event => {
		let userInput = (event && event.target && event.target.checked) || false;
		
		setChecked(userInput);
		onChange && onChange(props, userInput);
	}
	
	return (
		<Field {...props}>
			<FormControlLabel
				classes={{ root: classes.SwitchInline }}
				label={i18n.t(label)}
				control={<Switch
					classes={{ root: size === 'small' ? classes.SwitchSmall : classes.Switch }}
					checked={checked}
					disabled={disabled}
					onChange={handleSwitchChange}
					size={size} />}
				/>
				
		</Field>

	)
}