const _copy = require('rfdc')();

/**
 * Devices
 *
 *
 */
export default class Devices {

	/*
	 * holds the raw data
	 */
	static rawDevices = {};

	/*
	 * holds the devices
	 */
	static Devices = {};

	/*
	 * holds the groups
	 */
	static Groups = {};

	/*
	 * holds the groups IDs with its names
	 */
	static GroupList = [];
	
	/*
	 * holds the groups IDs
	 */
	static GroupIds = [];
	
	/**
	 * Sets the raw data
	 *
	 * @param	{Object}	rawDevices
	 * @return	Devices
	 */
	static set(rawDevices) {
		
		for (let deviceId in rawDevices) {
			Devices.rawDevices[deviceId] = { ...rawDevices[deviceId], id: deviceId };
		}
		
		return Devices;
	}
	
	/**
	 * Set devices
	 *
	 * @param	{Object}	devices
	 * @return	Devices
	 */
	static setDevices(devices) {
		
		Devices.Devices = devices;
		return Devices;
	}
	
	/**
	 * Set groups and group IDs
	 *
	 * @param	{Object}	groups
	 * @return	Devices
	 */
	static setGroups(groups) {
		
		// set groups
		Devices.Groups = groups;
		
		// set group Ids
		for (let groupId in groups) {
			Devices.addGroupId(groupId, groups[groupId].name);
		}
		
		return Devices;
	}
	
	/**
	 * Adds a group ID
	 *
	 * @param	{String}	id of group
	 * @param	{String}	name of group
	 * @return	Devices
	 */
	static addGroupId(id, name) {
		
		if (id && Devices.GroupIds.indexOf(id) === -1) {
			Devices.GroupList.push({ id, name });
			Devices.GroupIds.push(id);
		}
		
		return Devices;
	}
	
	/**
	 * Get devices
	 *
	 * @param	void
	 * @return	Object
	 */
	static getDevices(options = {}) {
		
		if (typeof options === 'boolean') {
			options = { raw: options }
		}
		
		const devices = options.raw ? Devices.rawDevices : Devices.Devices;
		return options.copy ? _copy(devices) : devices;
	}
	
	/**
	 * Get groups
	 *
	 * @param	void
	 * @return	Object
	 */
	static getGroups() {
		
		return Devices.Groups;
	}
	
	/**
	 * Get group IDs
	 *
	 * @param	void
	 * @return	Array
	 */
	static getGroupList() {
		
		return Devices.GroupList;
	}
}
