import React, { useState, useEffect } from 'react'
import i18n from '../../i18n'

import Autocomplete, { createFilterOptions } from '@material-ui/lab/Autocomplete'
import InputAdornment from '@material-ui/core/InputAdornment'
import TextField from '@material-ui/core/TextField'

import Field from './Field'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
}));


/*
 *
 */
export default function Text(props) {
	return props.options ? TextAutoComplete(props) : TextInput(props);
}

/*
 *
 */
export function TextInput(props) {
	const { onChange, id, required, label, type, children, select, shrink, disabled, size, placeholder, errorMessage, autoFocus, multiline, val, onEnter, disableUnderline, icon, InputProps, attributes, ...otherProps } = props;
	const classes = useStyles();
	
	const [value,setValue] = useState(val || '');
	const [error,setError] = useState(props.error ? props.error : (required && !val));
	useEffect(() => setError(props.error), [props.error]);
	
	const inputId = id || 'input-' + label.toLowerCase().replace(/ /g, '');
	
	return (
		<Field {...props} {...attributes}>
			<TextField
				classes={{ root: classes.TextField }}
				
				id={inputId}
				type={type || 'text'}
				required={required}
				select={select}
				
				multiline={multiline}
				rows={4}
				size={size}
				disabled={disabled}
				
				fullWidth={true}
				placeholder={placeholder || ''}
				autoFocus={autoFocus ? autoFocus : false}
				
				onKeyPress={e => {
					if (e.key === 'Enter' && !multiline) {
						e.preventDefault();
						onEnter && onEnter();
					}
				}}
				
				error={error}
				helperText={errorMessage ? i18n.t(errorMessage) : (required && error && i18n.t('No input given'))}
				
				label={Array.isArray(label) ? label.map(lbl => i18n.t(lbl)).join('') : i18n.t(label)}
				value={value}
				onChange={e => {
					setError(required && !e.target.value);
					setValue(e.target.value);
					onChange && onChange(props, e.target.value);
				}}
				
				InputProps={{
					disableUnderline: disableUnderline,
					startAdornment: icon && (
						<InputAdornment position="start">
							<span className={'mdi mdi-' + icon}></span>
						</InputAdornment>
					),
					...InputProps || {}
				}}
				
				InputLabelProps={{
					shrink: shrink ? shrink : true
				}}
				
				{...otherProps}
				>
				
				{children ? children : null}
			</TextField>
		</Field>

	)
}


/*
 *
 */
export function TextAutoComplete(props) {
	const classes = useStyles();
	const { error, required, autoFocus, placeholder, label,
				showMaxResults, onChange, val, attributes, ...otherProps } = props;
	
	const [value,setValue] = useState(val || []);
	//const [inputValue,setInputValue] = useState('');
	const filterOptions = showMaxResults && showMaxResults > 0 ? createFilterOptions({ limit: showMaxResults }) : undefined;
	const objectify = props.options && props.options[0] && props.options[0].id; // discover whether plain array of array of objects is used
	
	return (
		<Field {...props} {...attributes}>
			<Autocomplete
				classes={{ root: classes.Input, option: classes.AutoCompleteOption }}
				popupIcon={null}
				
				value={value}
				//defaultValue={val}
				//inputValue={inputValue}
				//onInputChange={(e, text) => setInputValue(text)}
				
				onChange={(e, option) => {
					option = option.map(val => typeof val === 'string' && objectify ? { id: val.toLowerCase().replace(/ /g, ''), name: val } : val);
					setValue(option);
					onChange && onChange(props, option);
				}}
				
				getOptionSelected={(option, value) => option && value ? (objectify ? option.id === value.id : option === value) : false}
				filterOptions={filterOptions}
				
				renderInput={params => <TextField
										{...params}
										error={error}
										required={required}
										autoFocus={autoFocus ? autoFocus : false}
										label={i18n.t(label) || i18n.t(placeholder)}
										/>}
				
				{...otherProps}
				/>
		</Field>
	
	);
}
