import React from 'react'
import { DEFAULTS } from './config/config'
import config from './config/config.dev.js'
import Log from './Log'
import i18n from './i18n'

import Connection from './Connection'
import Settings from './Settings'
import Devices from './Devices'
import Device from './Device'
import Jarvis from './Jarvis'

import Grid from '@material-ui/core/Grid'
import CircularProgress from '@material-ui/core/CircularProgress'
import Typography from '@material-ui/core/Typography'
import Skeleton from '@material-ui/lab/Skeleton'

import GridContainer from './components/GridContainer'
import StatusSnackbar from './components/StatusSnackbar'


/*
 * STYLES
 */
import { ThemeProvider, withStyles } from '@material-ui/core/styles';
const styles = theme => ({
	loading: {
		margin: '10px 0 0 0',
		color: '#999'
	},
	gridItem: {
		margin: theme.spacing(1, 1, 0, 0),
		[theme.breakpoints.down('md')]: {
			marginRight: theme.spacing(0.4, 0.4, 0, 0)
		}
	}
});


/*
 * CONSTANTS
 *
 */
class App extends React.Component {
	
    constructor(props) {
		super(props);
		
		this.state = {
			loaded: [],
			connected: false,
			error: false,
			errorMessage: '',
			
			theme: null
		}
		
	}
	
	componentDidMount() {
		
		// connect and get connection
		const errorHandler = err => {
			//Log._('App', i18n.t('Socket disconnected') + '!', 'error') //, true, err.stack);
			this.setState({
				connected: false,
				error: true,
				errorMessage: i18n.t(typeof err === 'string' ? err : err.message) + '. Reconnecting..'
			});
			
			/*
			setTimeout(function reconnect() {
				
				Log._('App', i18n.t('Attempting reconnect') + '..');
				Connection.reconnect();
				setTimeout(() => reconnect, 30*1000);
				
			}, 10*1000);
			*/
		};
		
		const url = window.location.port === '3000' ? config.iobrokerWebAdapterUrl :
			window.location.protocol + '//' + window.location.hostname + (window.location.port ? ':' + window.location.port : '');
		
		const listeners = [
			{ 'event': 'connect', 'callback': () => this.setState({ connected: true, error: false, errorMessage: '' }) },
			{ 'event': 'reconnect', 'callback': () => this.setState({ connected: 'pending', error: false, errorMessage: '' }) },
			{ 'event': 'disconnect', 'callback': errorHandler },
			{ 'event': 'error', 'callback': errorHandler }
		];
		
		Log._('App', i18n.t('Connect to') + ' ' + url + '...');
		this.socket = Connection.connect(url, listeners);
		
		// retrieve settings
		this.settings = {};
		this.socket.getState(DEFAULTS.NODE_SETTINGS)
			.then(state => {
				
				try {
					if (state) {
						this.settings = { ...this.settings, ...JSON.parse(state.val || {}) };
						Settings.setSettings(this.settings);
						Settings.applySettings();
					}
					
					const tab = Object.keys(this.settings.layout)[0];
					if (tab === undefined) {
						this.setState(prevState => ({
							loaded: [ ...prevState.loaded, 'settings' ],
							error: true,
							errorMessage: i18n.t('Please create the configuration. See documentation.')
						}));
					}
					else {
						this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'settings' ] }));
					}
				}
				catch(err) {
					Log._('App', 'GET_SETTINGS: ' + err.message, 'error');
					this.setState(prevState => ({
						loaded: [ ...prevState.loaded, 'settings' ],
						error: true,
						errorMessage: i18n.t('Error parsing settings') + '!'
					}));
				}
			})
			.catch(err => {
				Log._('App', 'GET_SETTINGS: ' + err.message, 'error');
				this.setState(prevState => ({
					loaded: [ ...prevState.loaded, 'settings' ],
					error: true,
					errorMessage: typeof err === 'string' ? i18n.t(err) : i18n.t(err.message)
				}));
			});
		
		// retrieve ioBroker config
		this.settings.iobroker = {};
		this.socket.getObject('system.config', (err, data) => {
			if (data && data.common) {
				this.settings.iobroker = data.common;
				Settings.setSettings({ iobroker: this.settings.iobroker });
			}
		});
		
		// get theme
		import('./theme').then(theme => this.setState({ theme: theme.default }));
		
		// retrieve devices
		this.socket.getState(DEFAULTS.NODE_DEVICES)
			.then(state => {
				let rawDevices = null;
				
				try {
					rawDevices = JSON.parse(state.val || {});
					this.processDevices(rawDevices).then(res => {
						Devices.set(rawDevices).setDevices(res.devices).setGroups(res.groups);
						this.setState(prevState => ({ loaded: [ ...prevState.loaded, 'groups' ] }));
					});
				}
				catch(err) {
					Log._('App', 'GET_DEVICES: ' + err.message, 'error');
					this.setState(prevState => ({
						loaded: [ ...prevState.loaded, 'groups' ],
						error: true,
						errorMessage: i18n.t('Error parsing devices') + '!'
					}));
				}
			})
			.catch(err => {
				Log._('App', 'GET_DEVICES: ' + err.message, 'error');
				this.setState(prevState => ({
					loaded: [ ...prevState.loaded, 'groups' ],
					error: true,
					errorMessage: i18n.t('Socket failed while getting devices') + '!'
				}));
			});
		
    }
	
	/**
	 *
	 *
	 */
	groupDevice(groups, device) {
		
		// add device to group(s)
		let options = device.get('options');
		options.group.forEach(group => {
			
			let groupId = group.toLowerCase().replace(/ /g, '');
			if (!groups[groupId]) {
				groups[groupId] = { 'id': groupId, 'name': group, 'devices': [device] };
			}
			else {
				groups[groupId].devices.push(device);
			}
		});
		
		return groups;
	}
	
	/**
	 *
	 *
	 */
	processDevice(deviceProperties) {
		
		let promises = [];
		let device = null;
		try {
			device = new Device(deviceProperties, this.socket);
			
			// request primary state value
			promises.push(device.requestDeviceState(null, true));
			
			// request secondary state value
			if (device.secondaryStateKey) {
				promises.push(device.requestDeviceState(device.secondaryStateKey, true));
			}
			
		}
		catch(err) {
			Log._('App', err.message, 'warn');
		}
		
		return new Promise(resolve => {
			Promise.allSettled(promises).then(results => {
				
				results.forEach(result => {
					if (result.status === 'rejected') {
						Log._('App', result, 'error');
					}
				});
				
				resolve(device);
			});
		});
	}
	
	/**
	 *
	 *
	 */
	processDevices(deviceList) {
		
		// loop through all devices of in the device list
		let promises = [];
		let devices = {};
		let groups = {};
		
		let processedDevices = 0, groupedDevices = 0;
		for (let deviceId in deviceList) {
			processedDevices++;
			
			deviceList[deviceId].options = { ...deviceList[deviceId].options, ...deviceList[deviceId].jarvis }
			delete deviceList[deviceId].jarvis;
			
			let deviceProperties = { ...deviceList[deviceId], 'id': deviceId };
			promises.push(new Promise((resolve, reject) => {
				
				this.processDevice(deviceProperties).then(device => {
			
					if (device !== null) {
						groupedDevices++;
						groups = this.groupDevice(groups, device);
						devices[device.id] = device;
						resolve(deviceId);
					}
					
					reject(deviceId);
				});
			}));
			
			// check states for own device settings
			for (let stateKey in deviceProperties.states) {
				
				let state = deviceProperties.states[stateKey];
				if (state && state.jarvis !== undefined) {
					
					promises.push(new Promise((resolve, reject) => {
						
						this.processDevice({ ...deviceProperties, 'id': deviceId + '#' + stateKey, 'states': { [stateKey]: state }, 'options': state.jarvis }).then(device => {
					
							if (device !== null) {
								groupedDevices++;
								groups = this.groupDevice(groups, device);
								devices[device.id] = device;
								resolve(deviceId);
							}
							
							reject(deviceId);
						});
					}));
					
				}
			}
		}
		
		return new Promise(resolve => Promise.allSettled(promises).then(res => {
			
			// sort groups
			for (let groupId in groups) {
				groups[groupId].devices.sort((item1, item2) => {
					if (item1.options.sort > item2.options.sort) return 1;
					else if (item2.options.sort > item1.options.sort) return -1;
					else return 0;
				});
			}
		
			// return
			Log._('App', [ 'Processed ' + processedDevices + ' devices. Added ' + groupedDevices + ' devices in ' + Object.keys(groups).length + ' groups.', res, groups ]);
			resolve({ devices: devices, groups: groups });
		}));
	}
	
	renderError(error) {
		error = error || this.state.errorMessage;
		Log._('App', 'Render Error: ' + error, 'error');
		
		return (

<StatusSnackbar
	key={'StatusSnackbar'}
	variant="error"
	closeButton={false}
	message={error.toString()}
	/>

		);
	}
	
	renderLoadingProgress() {
		const { classes } = this.props;
		return (

<Grid
	key="loadingProcess"
	container
	spacing={0}
	direction="column"
	justify="center"
	alignItems="center"
	style={{ minHeight: "100vh" }}
	>

	<CircularProgress />
	<Typography className={classes.loading}>{i18n.t('Retrieving settings')}...</Typography>

</Grid>

		);
	}
	
	renderLoadingSkeleton(props) {
		const { classes } = this.props;
		const { topBar, tabBar, gridColumns } = props;
		
		const SkeletonLoader = (props) => <Skeleton animation="wave" variant="rect" className={classes.gridItem} height={props.height} />;
		
		let gridContents = {}
		for (let column = 1; column <= gridColumns; column++) {
			gridContents[column] = gridContents[column] || [];
			
			let loaders = Math.random() * (5 - 2) + 2;
			for (let loader = 1; loader <= loaders; loader++) {
				gridContents[column].push(<SkeletonLoader key={column + '#' + loader} height={(Math.random() * (400 - 100) + 100)} />);
			}
		}
		
		return (

<React.Fragment>
	{topBar && <Skeleton animation="wave" variant="rect" height={64} />}
	{tabBar && <Skeleton animation="wave" variant="rect" height={48} />}
	<GridContainer key="loadingSkeleton" contents={gridContents} />
</React.Fragment>

		);
	}
	
    render() {
		
		// error message
		const error = this.state.error ? this.renderError() : null;
		
		// loading screens
		if (!error && this.state.loaded.indexOf('settings') === -1) {
			return this.renderLoadingProgress();
		}
		else if (!error && this.state.loaded.indexOf('groups') === -1) {
			const tab = Object.keys(this.settings.layout)[0];
			return tab === undefined ? null : this.renderLoadingSkeleton({ topBar: true, tabBar: tab !== 1, gridColumns: (tab !== 1 ? Object.keys(this.settings.layout[tab]).length : tab.length) });
		}
		
		// App
		else {
			Log._('App', [ 'Connected:', this.state.connected ]);
			return (

<ThemeProvider theme={this.state.theme}>
	{error}
	{this.state.loaded.length === 2 && <Jarvis key="jarvis" connectionStatus={this.state.connected} />}
</ThemeProvider>

			)
		}
	}
}

export default withStyles(styles)(App);
