import React from 'react'
//import clsx from 'clsx'
import { v4 as uuid } from 'uuid'

import Devices from '../../../Devices'
import i18n from '../../../i18n'

import IconButton from '@material-ui/core/IconButton'

import TableBody from '@material-ui/core/TableBody'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'

import AddIcon from '@material-ui/icons/AddCircle'
import EditIcon from '@material-ui/icons/Edit'

import EnhancedTable from '../../../components/EnhancedTable'
import Inputs from '../../../components/Inputs'

import DevicesPage from './DevicesPage'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	deleteButton: {
		marginTop: '25px'
	},
	hr: {
		margin: '10px 2px'
	},
	CustomTextInput: {
		width: 150
	},
	CustomDialogTitle: {
		padding: 0,
		marginRight: '50px'
	},
	TableCell: {
		verticalAlign: 'top',
		padding: '5px 0'
	}
});


class SettingsPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			selectedDevice: null,
			errors: {}
		}
		
		this.devices = Devices.getDevices({ raw: true, copy: true });
	}
	
	/*
	 *
	 */
	_saveDevices = devices => {
		this.devices = devices;
		this.props.onChange && this.props.onChange(devices);
	}
	
	/*
	 *
	 */
	_saveDevice = device => {
		this._saveDevices({ ...this.devices, [device.id]: device });
		
		this.setState({ selectedDevice: null, errors: {} }, () => document.getElementById('DialogContent').scrollTop = (document.getElementById('device-' + device.id) && document.getElementById('device-' + device.id).offsetTop) || 0);
	}
	
	/*
	 *
	 */
	_deleteDevice = deviceId => {
		let nextDevices = { ...this.devices }
		delete nextDevices[deviceId];
		this._saveDevices(nextDevices);
		
		this.setState({ selectedDevice: null, errors: {} });
	}
	
	/*
	 *
	 */
	renderDevices(props) {
		const { sortedRows } = props;
		
		return (
			<TableBody>
			
			{sortedRows.map((device, index) => {
				let options = Object.keys(device.options || {}).filter(optionKey => (optionKey === 'group' || device.options[optionKey] === '' || (Array.isArray(device.options[optionKey]) && device.options[optionKey].length === 0) || (typeof device.options[optionKey] === 'object' && Object.keys(device.options[optionKey]).length === 0)) === false).sort().join(', '); 
				let attributes = Object.keys(device.attributes || {}).sort().join(', ');
				let group = (device.options && device.options.group && device.options.group.join(', ')) || '';
				
				return (
				<TableRow key={'TableRow-' + index} id={'device-' + device.id} hover tabIndex={-1}>
					<TableCell padding="checkbox" scope="row" align="center">
						<IconButton onClick={() => this.setState({ selectedDevice: device.id })}>
							<EditIcon />
						</IconButton>
					</TableCell>
					<TableCell>{(device.options && device.options.label) || device.name}</TableCell>
					<TableCell>{device.function}</TableCell>
					<TableCell>{group}</TableCell>
					<TableCell>{Object.keys(device.states || {}).join(', ')}</TableCell>
					<TableCell>{options.length > 30 ? options.substr(0,27) + '...' : options}</TableCell>
					<TableCell>{attributes.length > 15 ? attributes.substr(0,12) + '...' : attributes}</TableCell>
				</TableRow>
				
				);
			})}
			
			</TableBody>
		);
	}
	
	/*
	 *
	 */
	render() {
		return (

<React.Fragment>
	<EnhancedTable
		title={i18n.t('Devices List')}
		headCells={[
			{ id: 'edit', width: '3%', disablePadding: true, label: '' },
			{ id: 'name', width: '13%', label: i18n.t('Name') },
			{ id: 'function', width: '8%', label: i18n.t('Function') },
			{ id: 'group', orderBy: 'options.group', width: '7%', label: i18n.t('Group') },
			{ id: 'states', width: '45%', disablePadding: false, label: i18n.t('States') },
			{ id: 'options', width: '14%', disablePadding: false, label: i18n.t('Options') },
			{ id: 'attributes', width: '10%', disablePadding: false, label: i18n.t('Attributes') }
		]}
		rows={Object.values(this.devices)}
		
		defaultOrderBy="name"
		selectable={false}
		sortable={true}
		stickyHeader
		
		CustomTableBody={props => this.renderDevices(props)}
		toolbarRightActionButton={
			<Inputs.Button
				onClick={() => this.setState({ selectedDevice: '#new' })}
				startIcon={<AddIcon />}
				disabled={this.state.selectedDevice !== null}
				label={i18n.t('add new device')}
				/>
		}
		/>
	
	{this.state.selectedDevice && <DevicesPage
									device={this.state.selectedDevice === '#new' ? { id: uuid(), states: {}, options: {} } : this.devices[this.state.selectedDevice]}
									onSave={this._saveDevice}
									onDelete={this._deleteDevice}
									onClose={() => this.setState({ selectedDevice: null })}
									/>}
	
</React.Fragment>

		)
	}
}

export default withStyles(styles)(SettingsPage);
