import React from 'react'
import clsx from 'clsx'
import i18n from '../../i18n'

import Grid from '@material-ui/core/Grid'
import Tooltip from '@material-ui/core/Tooltip'
import InfoIcon from '@material-ui/icons/Info'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	Field: {
		display: 'inline-flex',
		marginLeft: theme.spacing(1),
		marginRight: theme.spacing(1),
		padding: '0 !important',
	},
	CustomField: {
	},
}));


/*
 *
 */
export default function Field(props) {
	const { children, id, info, link, width } = props;
	const classes = useStyles(props);
	
	return (
		<Grid
			key={'grid-' + id}
			className={clsx(classes.Field, classes.CustomField)}
			style={{ width: width || 250 }}
			container
			item
			>
			
			{i18n.t(info) && <Grid item style={{ width: 30 }}>
				<a target="_blank" rel="noopener noreferrer" href={link}>
					<Tooltip title={i18n.t(info)}>
						<InfoIcon color="primary" />
					</Tooltip>
				</a>
			</Grid>}
			<Grid item style={{ width: info ? 250-30 : '100%' }}>
				{children}
			</Grid>
		</Grid>

	);
}