import React from 'react'

import SettingsConfig from './Settings.config'
import Settings from '../../../Settings'

import Accordion from '@material-ui/core/Accordion'
import AccordionDetails from '@material-ui/core/AccordionDetails'
import AccordionSummary from '@material-ui/core/AccordionSummary'
import Typography from '@material-ui/core/Typography'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	heading: {
		fontSize: theme.typography.pxToRem(15),
		flexBasis: '33.33%',
		flexShrink: 0,
	},
	secondaryHeading: {
		fontSize: theme.typography.pxToRem(15),
		color: theme.palette.text.secondary,
	},
});


class SettingsPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		delete Settings.settings.iobroker;
		this.state = {
			panel: false
		}
		
		this.onChange = this.props.onChange;
		this.settings = Settings.getSettings();
	}
	
	/**
	 *
	 */
	_handleSettingsChange = (key, val) => {
		Settings.setSetting(key, val);
		
		if (this.onChange) {
			this.onChange(Settings.getSettings());
		}
	}
	
	/**
	 *
	 */
	_handlePanelChange = panel => (event, isExpanded) => this.setState({ panel: isExpanded ? panel : false });
	
	/*
	 *
	 */
	renderPanel(key, panel) {
		const { classes } = this.props;
		
		const settings = Object.keys(panel.settings).map(inputKey => {
			const inputSettings = panel.settings[inputKey];
			const Tag = Inputs[inputSettings.type || 'Text'];
			
			return <Tag
						key={'Input-' + key + '-' + inputKey}
						id={inputKey}
						onChange={this._handleSettingsChange}
						val={this.props.settings[inputKey] || this.settings[inputKey]}
						{...inputSettings}
						/>
		});
		
		return (

<Accordion
	key={'Accordion-' + key}
	expanded={this.state.panel === key}
	onChange={this._handlePanelChange(key)}
	>
	
	<AccordionSummary
		expandIcon={<ExpandMoreIcon />}
		id={'panel-header-' + key}
		>
		
		<Typography className={classes.heading}>{panel.label}</Typography>
		{panel.description && <Typography className={classes.secondaryHeading}>{panel.description}</Typography>}
		
	</AccordionSummary>
	<AccordionDetails>
		{settings}
	</AccordionDetails>
</Accordion>

		)
	}
	
	/*
	 *
	 */
	render() {
		return Object.keys(SettingsConfig).map(panel => this.renderPanel(panel, SettingsConfig[panel]))
	}
}

export default withStyles(styles)(SettingsPage);
