import React from 'react'

import Chip from '@material-ui/core/Chip'
import { TextAutoComplete } from './Text'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles((theme) => ({
	chip: {
		margin: theme.spacing(0.5),
	},
}));


/*
 *
 */
export default function InputChip(props) {
	const { icon, color, label, onDelete, deleteIcon } = props;
	const classes = useStyles(props);
	
	const handleDelete = () => onDelete && onDelete();
	return (
		<Chip
			className={classes.chip}
			icon={icon}
			color={color}
			label={label}
			onDelete={handleDelete}
			deleteIcon={deleteIcon}
			/>
	)
}

/*
 *
 */
export function InputChips(props) {
	const { list, ...otherProps } = props;
	
	const chips = Array.isArray(list) ? list : list.split(',');
	return chips.map(chip => typeof chip === 'string' ? InputChip({ label: chip, ...otherProps }) : InputChip({ ...chip, ...otherProps }));
}

/*
 *
 */
export function InputChipsField(props) {
	const { optionKey, ...otherProps } = props;
	
	return (
		<TextAutoComplete
			multiple
			freeSolo
			getOptionLabel={option => optionKey ? option[optionKey] : option}
			{...otherProps}
			/>
	);
}
