import React from 'react'

import { DEFAULTS } from './config/config'
import Log from './Log'
import Connection from './Connection'
import Settings from './Settings'
import Devices from './Devices'

import Hidden from '@material-ui/core/Hidden'
//import { SwipeableViews } from '@tarragon/swipeable-tabs'

import Pages from './pages'
import TopBar from './components/TopBar'
import TabBar, { TabPanel } from './components/TabBar'
import Notifications from './components/Notifications'
import GridContainer from './components/GridContainer'
import Popup from './components/Popup'
import Widget from './components/Widget'
import Modules, { ModuleSettings } from './modules'


/*
 *
 */
const getGroup = (groupId, group, props = {}) => {
	
	// group empty
	if (!group && ModuleSettings[group.settings.component].noDevicesAllowed !== true) {
		Log._('Jarvis', 'Group ' + groupId + ' defined in columns but not assigned to any devices!', 'error');
		return null;
	}
	
	// get module for component
	const Tag = Modules[group.settings.component || 'StateList'];
	
	// get tag
	if (Tag === undefined) {
		Log._('Jarvis', 'Undefined component "' + group.settings.component + '" used!', 'error');
		return null;
	}
	
	return <Tag key={'Tag' + groupId} devices={group.devices || []} openDialog={props.action ? props.action : null} settings={{ ...group.settings, ...props }} />
}


/*
 *
 */
const getGridContents = (contents, groups, props = {}) => {
	
	let gridContents = {};
	const columns = Object.keys(contents).length;
	
	for (let column = 1; column <= columns; column++) {
		let columnContents = contents[column];
		gridContents[column] = gridContents[column] || [];
		
		// no contents, thus add placeholder
		if (!columnContents || !Object.keys(columnContents).length) {
			gridContents[column].push(null);
		}
		
		// add contents
		else {
			for (let groupId in columnContents) {
				
				let group = groups[groupId] || {};
				group.devices = group.devices || [];
				group.settings = columnContents[groupId] || {};
				let moduleSettings = ModuleSettings[group.settings.component] || {};
				
				let content = getGroup(groupId, group, props);
				if ((group.devices.length > 0 || moduleSettings.noDevicesAllowed) && content) {
					gridContents[column].push(
						<Widget
							key={'Box-' + groupId}
							title={group.settings.title === null ? null : (group.settings.title || group.name || moduleSettings.title)}
							icon={group.settings.icon === null ? null : (group.settings.icon || moduleSettings.icon)}
							iconStyle={group.settings.iconStyle}
							>
							
							{content}
						</Widget>
					);
				}
			}
		}
	}
	
	return gridContents;
}


export default class Jarvis extends React.Component {
	
    constructor(props) {
		super(props);
		
		this.state = {
			selectedTab: 0,
			tabTitle: '',
			
			page: 'home',
			
			notificationDrawer: false,
			notifications: [],
			notificationsUnread: 0,
			
			dialog: false,
			dialogContents: {
				'ts': 0,
				'device': {}
			}
		}
		
		// bind methods
		this.closeDialog = this.closeDialog.bind(this);
		this.openDialog = this.openDialog.bind(this);
		this.onSelectTab = this.onSelectTab.bind(this);
		this.toggleDrawer = this.toggleDrawer.bind(this);
		this.navigate = this.navigate.bind(this);
		
		// get connection
		this.socket = Connection.getConnection;
		
		// initialise
		this.gridContents = {};
		this.devices = Devices.getDevices();
		this.layout = Settings.getSetting('layout');
		this.tabs = Object.keys(this.layout);
		this.tabsEnabled = Number.isNaN(parseInt(this.tabs[0]));
		this.state.tabTitle = this.tabs[this.state.selectedTab];
		
		// no tabs defined, so layout wrap into default tab
		if (!this.tabsEnabled) {
			this.layout = { 'Home': this.layout };
			this.tabs.push('1');
		}
		
		// widget props
		this.widgetProps = {}
	}
	
	componentDidMount() {
		
		// check if devices and settings are defined
		if (Object.keys(this.devices).length === 0 || Settings.getSetting('layout') === '') {
			this.navigate('configuration');
		}
		
		// get notifications
		this.socket.getState('jarvis.0.notifications').then(notifications => {
			notifications = (notifications && notifications.val && JSON.parse(notifications.val)) || [];
			
			this.setState({
				notificationsUnread: notifications.filter(notification => !notification.unread || notification.unread !== false).length,
				notifications: notifications
			});
		});
		
		this.socket.subscribeState(DEFAULTS.NODE_NOTIFICATIONS, notifications => Log._('Jarvis', notifications));
	}
	
	getTabPanels() {
		const groups = Devices.getGroups();
		
		let tabIndex = 0;
		let tabPanels = [];
		
		for (let tabLabel in this.layout) {
			const contents = this.layout[tabLabel];
			
			// fullscreen in component
			const groupId = Object.keys(contents)[0];
			const fullscreen = Number.isNaN(parseInt(groupId));
			
			if (!this.gridContents[tabLabel] && fullscreen) {
				this.gridContents[tabLabel] = getGroup(contents, { ...groups[groupId], settings: contents[groupId] || {} }, { action: this.openDialog, ...this.widgetProps });
			}
			
			// grid layout
			else if (!this.gridContents[tabLabel]) {
				this.gridContents[tabLabel] = <GridContainer contents={getGridContents(contents, groups, { action: this.openDialog, ...this.widgetProps })} />
			}
			
			const tabPanel = (
				<TabPanel key={'tabPanel-' + tabIndex} renderedTab={tabIndex} selectedTab={this.state.selectedTab} fullscreen={fullscreen}>
					{this.gridContents[tabLabel]}
				</TabPanel>
			);
			
			tabPanels.push(tabPanel);
			tabIndex++;
		}
		
		return tabPanels;
	}
	
	closeDialog() {
		this.setState({ 'dialog': false, 'dialogContents': { 'ts': Date.now(), 'device': {} } });
	}
	
	openDialog(device) {
		this.setState({ 'dialog': true, 'dialogContents': { 'ts': Date.now(), 'device': device } });
	}
	
	onSelectTab(tab) {
		this.setState({ selectedTab: tab, tabTitle: this.tabs[tab] });
	}
	
	toggleDrawer(drawer, open) {
		this.setState({ [drawer]: open });
	}
	
	navigate(page) {
		this.setState({ page });
	}
	
    render() {
		let Page = Pages[this.state.page] || null;
		
		const tabPanels = this.getTabPanels();
        return (

<React.Fragment>
	<TopBar
		title={Settings.topBarTitle || (this.tabsEnabled && this.state.tabTitle) || Settings.getSetting('pageTitle') || 'jarvis - Smart Home Overview'}
		connectionStatus={this.props.connectionStatus}
		toggleDrawer={this.toggleDrawer}
		navigate={this.navigate}
		notifications={this.state.notificationsUnread}
		>
		
		{this.tabsEnabled && <TabBar tabs={this.tabs} selectedTab={this.state.selectedTab} onSelectTab={this.onSelectTab} />}
	</TopBar>
	
	
	{/* DRAWER: NOTIFICATIONS */}
	<Notifications
		open={this.state.notificationDrawer}
		toggleDrawer={this.toggleDrawer}
		notifications={this.state.notifications}
		/>
	
	
	{/* PAGE */}
	{Page && <Page
		page={this.state.page}
		navigate={this.navigate}
		devices={this.devices}
		/>}
	
	
	{/* POPUP */}
	<Popup
		open={this.state.dialog}
		contents={this.state.dialogContents}
		closeDialog={this.closeDialog}
		/>
	
	
	{/* TABS */}
{/*
	<Hidden smUp>
	<SwipeableViews
		views={tabPanels}
		selectedView={this.state.selectedTab}
		onSwipe={this.onSelectTab}
		/>
	</Hidden>
	<Hidden smDown>
		{tabPanels}
	</Hidden>
*/}
	{tabPanels}
		
</React.Fragment>

		)
	}
}
