import React from 'react'
import i18n from '../../i18n'

import MenuItem from '@material-ui/core/MenuItem'

import Text from './Text'


/*
 *
 */
export default function InputSelect(props) {
	const { id, selection, val } = props;
	
	let value = '';
	const menuOptions = selection.map(option => {
		const key = Object.keys(option).shift();
		
		if (key === val) {
			value = val;
		}
		
		return <MenuItem key={'MenuItem-' + id + '-' + key} value={key}>{i18n.t(option[key])}</MenuItem>
	});
	
	return (
		<Text
			{...props}
			val={value}
			select={true}
			>
			
			{menuOptions}
		</Text>
	);
}