import React from 'react'
import clsx from 'clsx'

import Dialog from '@material-ui/core/Dialog'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	closeButton: {
		position: 'absolute',
		right: theme.spacing(1),
		top: theme.spacing(1),
		color: theme.palette.grey[500],
	},
	Dialog: {
	},
	DialogDense: {
		padding: theme.spacing(2, 0)
	},
	CustomDialog: {
	},
	DialogTitle: {
	},
	CustomDialogTitle: {
	},
	DialogTitleTypography: {
		//padding: theme.spacing(2)
	},
	DialogContent: {
		backgroundColor: '#fafafa'
	},
	CustomDialogContent: {
	},
	DialogActions: {
		margin: 0,
		padding: theme.spacing(1),
	},
	CustomDialogActions: {
	}
}));


export default function CustomDialog(props) {
	const { header, onClose, children, actions, dense, disableBackdropClick, disableEscapeKeyDown } = props;
	const classes = useStyles(props);
	
	return (

<Dialog
	classes={{ paper: clsx(classes.Dialog, classes.CustomDialog) }}
	fullWidth={props.fullWidth}
	maxWidth={props.maxWidth}
	
	disableBackdropClick={disableBackdropClick ? disableBackdropClick : true}
	disableEscapeKeyDown={disableEscapeKeyDown ? disableEscapeKeyDown : true}
	
	open={props.open}
	onClose={props.onClose}
	>
	
	<DialogTitle
		classes={{ root: clsx(classes.DialogTitle, classes.CustomDialogTitle) }}
		disableTypography
		>
		
		{typeof header !== 'string' ? header : <Typography className={classes.DialogTitleTypography} variant="h6">
			{header}
		</Typography>}
		<IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
			<CloseIcon />
		</IconButton>
	</DialogTitle>
	
	<DialogContent id='DialogContent' className={clsx(classes.DialogContent, classes.CustomDialogContent, dense && classes.DialogDense)} dividers>
		{children}
	</DialogContent>
	
	<DialogActions classes={{ root: clsx(classes.DialogActions, classes.CustomDialogActions) }}>
		{actions}
	</DialogActions>
</Dialog>

	);
}
