import React from 'react'

import { DEFAULTS } from '../../../config/config'
import LayoutsConfig from './Layouts.config'

import i18n from '../../../i18n'
import Devices from '../../../Devices'
import { ModuleConfigurations, ModuleSettings } from '../../../modules/'

import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

import AddIcon from '@material-ui/icons/AddCircle'
import TabIcon from '@material-ui/icons/Tab'
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import FullscreenIcon from '@material-ui/icons/Fullscreen'
import DeleteIcon from '@material-ui/icons/Delete'

import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	deleteButton: {
		marginTop: '20px'
	},
	tabs: {
		marginBottom: '10px'
	},
	tab: {
		padding: theme.spacing(3, 2),
	},
	iconContainer: {
		flexDirection: 'column',
		alignItems: 'center'
	},
	columnContainer: {
		marginTop: theme.spacing(2),
	},
	column: {
		width: 250,
		margin: theme.spacing(1, 1),
		borderRight: '1px solid rgba(0, 0, 0, 0.12)',
		borderLeft: '1px solid rgba(0, 0, 0, 0.12)',
	},
	DialogActions: {
		justifyContent: 'initial'
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	}
});


class LayoutsPage extends React.Component {
	
	constructor(props) {
		super(props);
		
		this.state = {
			popup: false,
			
			selectedTab: null,
			selectedColumn: null,
			selectedGroup: {},
			
			layout: this.props.layout,
			settings: this.props.settings,
			errors: {}
		}
		
		this.groupIds = Devices.getGroupList();
		this.groupEditor = {};
	}
	
	/**
	 *
	 */
	_onChange = (props, val) => {
		
		if (props.required && !val) {
			this.setState(prevStates => ({
				errors: {
					...prevStates.errors,
					[props.id]: i18n.t('No input given')
				}
			}));
			
			return false;
		}
		
		if (this.state.errors[props.id]) {
			this.setState(prevStates => {
				let nextErrors = { ...prevStates.errors || {} }
				delete nextErrors[props.id];
				return { errors: nextErrors };
			});
		}
		
		this.groupEditor[props.id] = val;
	}
	
	/**
	 *
	 */
	_addColumn = (tabKey, newColumn) => {
		
		this.setState(prevState => ({
			layout: {
				...prevState.layout,
				[tabKey]: {
					...prevState.layout[tabKey],
					[newColumn]: {}
				}
			}
		}), () => this.props.onChange(this.state.layout, this.state.settings));
	}
	
	/**
	 *
	 */
	_addTab = () => {
		const newTabIndex = Object.keys(this.state.layout).length+1;
		this.setState(prevState => ({
			layout: {
				...prevState.layout,
				['home|' + i18n.t('new tab') + ' ' + newTabIndex]: {
					1: {}
				}
			}
		}), () => this.props.onChange(this.state.layout, this.state.settings));
		
		setTimeout(() => window.location = '#tab-' + newTabIndex, 100);
	}
	
	/**
	 *
	 */
	_changeTab = tabKey => ({ id }, userInput) => {
		
		// edit tab icon / label
		if (id === 'tabIcon' || id === 'tabLabel') {
			
			// icon or label
			let [ icon, label ] = tabKey.split('|');
			if (id === 'tabIcon') {
				icon = userInput;
			}
			else if (id === 'tabLabel') {
				label = userInput;
			}
			
			let layout = {}
			Object.keys(this.state.layout).forEach(tKey => {
				layout[tKey === tabKey ? icon + '|' + label : tKey] = this.state.layout[tKey];
			});
			
			this.setState({ layout: layout }, () => this.props.onChange(this.state.layout, this.state.settings));
		}
		
		// edit tab fullscreen option
		else if (id === 'tabFullscreen') {
			let prevTab = { ...this.state.layout[tabKey] }
			
			if (userInput === true) {
				this.setState(prevState => ({
					layout: {
						...prevState.layout,
						[tabKey]: prevTab[1]
					}
					
				}), () => this.props.onChange(this.state.layout, this.state.settings));
			}
			else {
				this.setState(prevState => ({
					layout: {
						...prevState.layout,
						[tabKey]: {
							1: prevTab
						}
					}
					
				}), () => this.props.onChange(this.state.layout, this.state.settings));
			}
		}
	}
	
	/**
	 *
	 */
	_closePopup = action => {
		
		if (action === 'cancel') {
			this.setState({ popup: false });
			return false;
		}
		else if (action === 'save') {
			
			let tabKey = this.state.selectedTab;
			let columnKey = this.state.selectedColumn;
			let groupKey = this.state.selectedGroup.key;
			
			// errors
			const requiredFields = [{ parameter: 'key', required: true }, { parameter: 'component', required: true }];
			let errors = {};
			
			[ ...requiredFields, ...(ModuleConfigurations[this.state.selectedGroup.key] || []) ].forEach(field => {
				errors['input-group-' + field.parameter] = field.required === true && !this.groupEditor['input-group-' + field.parameter];
			});
			
			if (Object.values(errors).indexOf(true) > -1) {
				this.setState(prevState => ({ errors: { ...prevState.errors, ...errors }}));
				console.error(errors, this.groupEditor);
				return false;
			}
			
			let group = {};
			Object.keys(this.groupEditor).forEach(key => group[key.replace('input-group-', '')] = this.groupEditor[key]);
			group.key = group.key.toLowerCase().replace(/ /g, '');
			
			// edit group
			if (groupKey) {
				
				let tabLayout = {}
				for (let col = 1; col < Object.keys(this.state.layout[tabKey]).length; col++) {
					tabLayout[col] = this.state.layout[tabKey][col >= columnKey ? col+1 : col];
				}
				
				let tabColumnGroups = {};
				Object.keys(this.state.layout[tabKey][columnKey]).forEach(componentKey => {
					
					if (componentKey === groupKey) {
						tabColumnGroups[group.key] = { ...this.state.layout[tabKey][columnKey][componentKey], ...group }
					}
					else {
						tabColumnGroups[componentKey] = this.state.layout[tabKey][columnKey][componentKey];
					}
				});
				
				delete tabColumnGroups[group.key].key;
				this.setState(prevState => ({
					popup: false,
					layout: {
						...prevState.layout,
						[tabKey]: {
							...prevState.layout[tabKey],
							[columnKey]: tabColumnGroups
						}
					}
					
				}), () => this.props.onChange(this.state.layout, this.state.settings));
			}
			
			// new group
			else {
				
				this.setState(prevState => ({
					popup: false,
					layout: {
						...prevState.layout,
						[tabKey]: {
							...prevState.layout[tabKey],
							[columnKey]: {
								...prevState.layout[tabKey][columnKey],
								[group.key]: group
							}
						}
					}
				}), () => this.props.onChange(this.state.layout, this.state.settings));
			}
		}
	}
	
	/**
	 *
	 */
	_deleteColumn = (tabKey, columnKey) => {
		
		let tabLayout = {}
		for (let col = 1; col < Object.keys(this.state.layout[tabKey]).length; col++) {
			tabLayout[col] = this.state.layout[tabKey][col >= columnKey ? col+1 : col];
		}
		
		this.setState(prevState => ({
			layout: {
				...prevState.layout,
				[tabKey]: tabLayout
			}
		}));
	}
	
	/**
	 *
	 */
	_deleteGroup = (tabKey, columnKey, groupKey) => {
		
		let layout = { ...this.state.layout[tabKey][columnKey] }
		delete layout[groupKey];
		
		this.setState(prevState => ({
			popup: false,
			layout: {
				...prevState.layout,
				[tabKey]: {
					...prevState.layout[tabKey],
					[columnKey]: layout
				}
			}
		}), () => this.props.onChange(this.state.layout, this.state.settings));
	}
	
	/**
	 *
	 */
	_deleteTab = tabKey => {
		
		let layout = { ...this.state.layout }
		delete layout[tabKey];
		
		this.setState({ layout: layout }, () => this.props.onChange(this.state.layout, this.state.settings));
	}
	
	/**
	 *
	 */
	_moveGroup = (action, tabKey, columnKey, componentKey) => event => {
		event.stopPropagation();
		
		let order = { ...this.state.layout[tabKey][columnKey] }
		let sorted = false;
		let sortedArray = Object.keys(order).sort((a, b) => {
			
			if (!sorted && ((action === 'up' && b === componentKey) || (action === 'down' && a === componentKey))) {
				sorted = true;
				return 1;
			}
			
			return 0;
		});
		
		let sortedObject = {}
		sortedArray.map(key => sortedObject[key] = order[key]);
		
		this.setState(prevState => ({
			layout: {
				...prevState.layout,
				[tabKey]: {
					...prevState.layout[tabKey],
					[columnKey]: sortedObject
				}
			}
		}), () => this.props.onChange(this.state.layout, this.state.settings));
	}
	
	/**
	 *
	 */
	_moveTab = (action, tabKey) => event => {
		event.stopPropagation();
		
		let order = { ...this.state.layout }
		let sorted = false;
		let sortedArray = Object.keys(order).sort((a, b) => {
			
			if (!sorted && ((action === 'up' && b === tabKey) || (action === 'down' && a === tabKey))) {
				sorted = true;
				return 1;
			}
			
			return 0;
		});
		
		let sortedObject = {}
		sortedArray.map(key => sortedObject[key] = order[key]);
		this.setState(prevState => ({ layout: sortedObject }), () => this.props.onChange(this.state.layout, this.state.settings));
	}
	
	/**
	 *
	 */
	_openPopup = (tabKey, columnKey, group) => {
		
		let states = {};
		Object.keys(group || {}).map(key => states['input-group-' + key] = group[key] || '');
		this.groupEditor = states;
		
		this.setState({
			...states,
			popup: true,
			errors: {},
			
			selectedTab: tabKey,
			selectedColumn: columnKey,
			selectedGroup: group || {}
		});
	}
	
	/*
	 *
	 */
	renderTabColumnGroup(tab, column, group) {
		const { classes } = this.props;
		
		return (

<ListItem
	key={'Tab-' + tab.key + '_Column-' + column.key + '_Component-' + group.key}
	onClick={() => this._openPopup(tab.key, column.key, group)}
	button
	divider
	>
	
	{tab.fullscreen ?
	<ListItemIcon classes={{ root: classes.iconContainer }}>
		<FullscreenIcon />
	</ListItemIcon>
		:
	<ListItemIcon classes={{ root: classes.iconContainer }}>
		<React.Fragment>
			<IconButton size="small" onClick={this._moveGroup('up', tab.key, column.key, group.key)}>
				<ArrowUpIcon />
			</IconButton>
			<IconButton size="small" onClick={this._moveGroup('down', tab.key, column.key, group.key)}>
				<ArrowDownIcon />
			</IconButton>
		</React.Fragment>
	</ListItemIcon>
	}
	<ListItemText primary={
		<React.Fragment>
			<Typography display="block">{group.key}</Typography>
			<Typography variant="subtitle2">({group.name})</Typography>
		</React.Fragment>
	} />
	
</ListItem>

		);
	}
	
	/*
	 *
	 */
	renderTabColumns(tab, column) {
		const { classes } = this.props;
		
		// components per tab
		let tabColumnComponentsNodes = [];
		
		for (let componentKey in column.entries) {
			let component = { ...column.entries[componentKey], key: componentKey, name: (column.entries[componentKey] && column.entries[componentKey].component) || DEFAULTS.MODULE };
			tabColumnComponentsNodes.push(this.renderTabColumnGroup(tab, column, component));
		}
		
		return (

<Grid className={classes.column} key={'Tab-' + tab.key + '_Column-' + column.key} item>
	<List>
		<ListItem onClick={() => this._deleteColumn(tab.key, column.key)} button divider dense>
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<IconButton size="small">
					<DeleteIcon />
				</IconButton>
			</ListItemIcon>
			<ListItemText primary={i18n.t('delete column')} />
		</ListItem>
		{(!tab.fullscreen || (tab.fullscreen && column.entries.length < 1))&& <ListItem onClick={() => this._openPopup(tab.key, column.key, null)} button divider dense>
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<IconButton size="small">
					<AddIcon />
				</IconButton>
			</ListItemIcon>
			<ListItemText primary={i18n.t('add new group')} />
		</ListItem>}
		
		{tabColumnComponentsNodes}
		
	</List>
</Grid>

		);
	}
	
	/*
	 *
	 */
	renderTab(tab) {
		const { classes } = this.props;
		const [ tabIcon, tabLabel ] = tab.key.split('|');
		tab.fullscreen = Number.isNaN(parseInt(Object.keys(tab.entries)[0]));
		
		// columns per tab
		let columns = Object.keys(tab.entries).length;
		let groupsInFirstColumn = (tab.entries && tab.entries[1] && Object.keys(tab.entries[1]).length) || 0;
		let tabColumnsNodes = [];
		
		for (let columnKey = 1; columnKey < columns+1; columnKey++) {
			let column = { entries: (tab.entries[columnKey] !== undefined ? tab.entries[columnKey] : tab.entries), key: columnKey }
			tabColumnsNodes.push(this.renderTabColumns(tab, column));
		}
		
		return (

<ListItem
	id={'tab-' + tab.index}
	divider={tab.lastTab !== true} key={'Tab-' + tab.key}
	classes={{ root: classes.tab }}
	>
	
	<ListItemIcon key={'Tab-' + tab.key + '-ListItemIcon'} classes={{ root: classes.iconContainer }}>
		<React.Fragment>
			<IconButton size="small" onClick={this._moveTab('up', tab.key)}>
				<ArrowUpIcon />
			</IconButton>
			<IconButton size="small" onClick={this._moveTab('down', tab.key)}>
				<ArrowDownIcon />
			</IconButton>
			<IconButton size="small" onClick={() => this._deleteTab(tab.key)} classes={{ root: classes.deleteButton }}>
				<DeleteIcon />
			</IconButton>
		</React.Fragment>
	</ListItemIcon>
	<ListItemText
		disableTypography={true}
		primary={[
			<Inputs.Text
				key='Input-tabIcon'
				id='tabIcon'
				{...LayoutsConfig.tabIcon}
				onChange={this._changeTab(tab.key)}
				val={tabIcon || ''}
				icon={tabIcon || ''}
				/>,
			
			<Inputs.Text
				key='Input-tabLabel'
				id='tabLabel'
				{...LayoutsConfig.tabLabel}
				onChange={this._changeTab(tab.key)}
				val={tabLabel || ''}
				/>,
			
			<Inputs.Button
				key='Input-addColumnToTab'
				id='Input-addColumnToTab'
				label='add new column'
				disabled={tab.fullscreen}
				onClick={() => this._addColumn(tab.key, columns+1)}
				/>,
			
			<Inputs.Switch
				key='Input-tabFullscreen'
				id='tabFullscreen'
				{...LayoutsConfig.tabFullscreen}
				disabled={(columns > 1 || groupsInFirstColumn > 1)}
				onChange={this._changeTab(tab.key)}
				val={tab.fullscreen}
				/>,
			
			<Grid
				key={'Tab-' + tab.key + '-ColumnContainer'}
				classes={{ root: classes.columnContainer }}
				container
				>
				
				{tabColumnsNodes}
			</Grid>
		]}
		/>
	
</ListItem>

		);
	}
	
	/*
	 *
	 */
	renderTabs(tabs) {
		let tab = 0;
		let tabsNodes = [];
		
		const tabsTotal = Object.keys(tabs).length;
		for (let tabKey in tabs) {
			tab++;
			tabsNodes.push(this.renderTab({ entries: tabs[tabKey], key: tabKey, index: tab, lastTab: tab === tabsTotal }));
		}
		
		return tabsNodes;
	}
	
	/*
	 *
	 */
	render() {
		const { classes } = this.props;
		
		const tabs = this.renderTabs(this.state.layout);
		let config = ModuleConfigurations[this.state.selectedGroup.component || DEFAULTS.MODULE].map(config => {
			const Input = Inputs[config.type || 'Text'];
			return (
				<Inputs.FieldsetItem columns={4} key={'FieldsetItem-' + config.parameter}>
					<Input
						key={'input-group-' + config.parameter}
						id={'input-group-' + config.parameter}
						label={i18n.t(config.label || config.parameter)}
						info={config.description}
						{...config}
						onChange={this._onChange}
						val={this.state.selectedGroup[config.parameter] || config.val}
						/>
		
				</Inputs.FieldsetItem>
			);
		});
		
		const filtered = this.groupIds.filter(group => group.id === this.state.selectedGroup.key);
		const groupName = filtered && filtered[0] && filtered[0].name;
		
		return (

<React.Fragment>
	<Paper>
		<List className={classes.tabs}>
			<ListItem divider classes={{ root: classes.tab }}>
				<ListItemIcon classes={{ root: classes.iconContainer }}>
					<TabIcon />
				</ListItemIcon>
				<ListItemText
					disableTypography={true}
					primary={[
						<Inputs.Button key='Input-addTabs' id='Input-addTabs' label='add new tab' onClick={() => this._addTab()} />,
						<Inputs.Switch
							key='Input-tabsHideLabels'
							id='tabsHideLabels'
							{...LayoutsConfig.tabsHideLabels}
							onChange={(id, userInput) => this.props.onChange(this.state.layout, { [id]: userInput })}
							val={this.state.settings['tabsHideLabels']}
							/>
					]}
					/>
				
			</ListItem>
			
			{tabs}
			
		</List>
	</Paper>
	
	<Dialog
		header={i18n.t('Group configuration')}
		classes={{ CustomDialogActions: classes.DialogActions }}
		actions={
			<Grid container>
				<Grid item xs={6}>
					{this.state.selectedGroup.key && 
					<Button key='action-delete' onClick={() => this._deleteGroup(this.state.selectedTab, this.state.selectedColumn, this.state.selectedGroup.key)} color="secondary" startIcon={<DeleteIcon />}>
						{i18n.t('Delete Group')}
					</Button>}
				</Grid>
				<Grid item xs={6} className={classes.alignRight}>
					<Button key='action-cancel' onClick={() => this._closePopup('cancel')} color="secondary">
						{i18n.t('Cancel')}
					</Button>
					<Button key='action-save' onClick={() => this._closePopup('save')} color="primary" variant="contained" disabled={Object.keys(this.state.errors).length > 0}>
						{i18n.t('Ok')}
					</Button>
				</Grid>
			</Grid>
		}
		
		dense
		maxWidth={'md'}
		open={this.state.popup}
		onClose={() => this._closePopup('cancel')}
		>
		
		<Inputs.Fieldset label={i18n.t('Widget-Box')}>
			{ModuleSettings[this.state.selectedGroup.component || DEFAULTS.MODULE].noDevicesAllowed !== true && <Inputs.FieldsetItem>
				<Inputs.Text
					key='input-group-key'
					id='input-group-key'
					{...LayoutsConfig.groupName}
					
					autoComplete
					autoHighlight
					
					val={this.state.selectedGroup.key ? ({ id: this.state.selectedGroup.key, name: groupName || this.state.selectedGroup.key }) : null}
					onChange={(props, group) => this._onChange(props, (group && group.id) || '')}
					options={this.groupIds}
					getOptionLabel={option => option['name']}
					//getOptionSelected={}
					
					required
					error={this.state.errors['input-group-key'] !== undefined}
					/>
				
			</Inputs.FieldsetItem>}
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text key='input-group-title' id='input-group-title' {...LayoutsConfig.groupTitle} onChange={this._onChange} val={this.state.selectedGroup.title || ''} />
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text key='input-group-icon' id='input-group-icon' {...LayoutsConfig.groupIcon} onChange={this._onChange} val={this.state.selectedGroup.icon || ''} />
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text key='input-group-iconStyle' id='input-group-iconStyle' {...LayoutsConfig.groupIconStyle} onChange={this._onChange} val={this.state.selectedGroup.iconStyle || ''} />
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
		
		<Inputs.Fieldset label={i18n.t('Module')}>
			<Inputs.FieldsetItem>
				<Inputs.Text
					key='input-group-component'
					id='input-group-component'
					{...LayoutsConfig.groupModule}
					
					autoComplete
					autoHighlight
					
					val={this.state.selectedGroup.component || DEFAULTS.MODULE}
					onChange={(props, val) => {
						this._onChange(props, val);
						this.setState(prevState => ({
							selectedGroup: {
								...prevState.selectedGroup,
								component: val
							}
						}));
					}}
					
					required
					error={this.state.errors['input-group-component'] !== undefined}
					/>
				
			</Inputs.FieldsetItem>
			{config}
		</Inputs.Fieldset>
	</Dialog>
	
</React.Fragment>

		)
	}
}

export default withStyles(styles)(LayoutsPage);
